(function($){
    $(window).on("load",function(){
        setTimeout(function(){
            $(".Loading").fadeOut();
        }, 1000);
        $(".content").mCustomScrollbar({
            theme:"minimal-dark",
            axis:"xy",
            scrollInertia:400,
            autoDraggerLength:true,
            advanced:{ 
                updateOnContentResize:true 
            }
        });
    });
})(jQuery);


